<template>
<div>
    <div class="container">
        <div class="Income-list result-block">
          <ul >
              <li class="Inter-income-list >" v-for="(Income, index) in Incomes" 
                  :key='index'><p class="p-description">{{Income.description}}
                      </p><p class="p-amount">{{Income.amount}} <b-icon-x-circle-fill v-on:click="removeItemFromIncomeList(index)" >
                          </b-icon-x-circle-fill></p></li>    
          </ul>
        </div>
        <div class="Expenses-list result-block">
            <ul>
              <li class="Inter-expense-list >" v-for="(Expense, index) in Expenses" 
                  :key='index'><p class="p-description">{{Expense.description}}
                      </p><p class="p-amount">{{Expense.amount}}<b-icon-x-circle-fill v-on:click="removeItemFromExpenseList(index)" ></b-icon-x-circle-fill></p></li>
          </ul>
        </div>
    </div>
</div>
    
</template>
<script>
import {mapActions} from 'vuex';
import {mapGetters} from  'vuex';
export default {
    computed:{
        ...mapGetters([
            'Incomes',
            'Expenses',
        ]), 
    },
    props:['selected','description','amount'],
methods:{
    ...mapActions({
        in:'addIncomeToList',
        ex:'addExpenseToList',
        remI:'removeIncome',
        remE:'removeExpense'
    }),
        addItemToList(){

            if(this.selected===null||this.description===null||this.amount===null){
                alert("Fill out all fields");
            }else{
                var d=this.description;
                var a=this.amount;
                if(this.selected==='inc'){
                    this.in({d,a});
                }else{  
                    this.ex({d,a});  
                }
            }   
           },
           removeItemFromIncomeList(e){
               this.remI(e);
            },
            removeItemFromExpenseList(e){
                this.remE(e);  
            }
        }
}   
</script>
<style>
    .Income-list{
        position: absolute;
        left:6%;
        margin-top:30px;
    }
    .Expenses-list{
        position: absolute;
        left:54%;
        margin-top:30px;
    }
    .Inter-income-list{
        color: white;
        border-radius: 5px;
        width:70vh;
        background: rgba(0, 151, 19, 0.6); 
        margin:5px;
        list-style-type: none;
    }
    .Inter-expense-list{
        color: white;
        border-radius: 5px;
        width:70vh;
        background: rgba(240, 54, 30, 0.445);
        margin:5px;
        list-style-type: none;
    }
    .p-description{
        position: absolute;
        padding-left:5px;
    }
    .p-amount{
    padding-left: 70%;
    }
    @media screen and (max-width: 800px){ }     
      
</style>